    margin: auto;
    display: flex;
    justify-content: space-around;
    list-style-type: none;
}
p.text-center {
    width: 80%;
}
.top{
    font-weight: bold;
    color: #C8B6A6;
    transition: 0.5s ease;
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;   
    
}
.top:hover{
    transform: scale(1.2);
    background-color: #C8B6A6;
    color: white;
    border-radius: 5px;
}
.nav-link {
    font-weight: bold;
    color: white;
    transition: 0.5s ease;
    cursor: pointer;
    text-decoration: none;
}
.nav-link:hover {
    transform: scale(1.2);
    background: #e92020;
    color: #080707;
    border-radius: 5px;
    padding: 10px;
}
